body{
    font-family: "Roboto";
  }
  
  .App {
    text-align: center;
  }
  
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .custom-drop{
    color: #3f4254;
    font-size: 12px;
    margin-left: 15px;
    
    padding: 9px 0px 9px 18px;
  }
  .flat-cions-2{
    color: #3F4254;
    font-size: 20px;
  }

  .custom-drop span{
    color: #000000;
    font-size: 12px;
  }

  .pro-item-content{
    margin-left: 7px;
  }
  .pro-item-content a{
    padding: 0px !important;
    padding-left: 5px !important;
  }
  .input-01{
    margin-top: 0px !important;
  }
  .App-link {
    color: #61dafb;
  }
  .label-custom-0 label{
    margin: 5px auto 0px !important;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .table>:not(caption)>*>* {
    padding: 0px;
    padding-left: 5px;
}

